.a {
  display: flex;
  height: 60vh;
  align-items: center;
}
.a-left {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.a-right {
  align-items: center;
  padding-left: 15%;
}
.a-card {
  width: 60%;
  height: 100vh;
  align-items: center;
  padding-left: 30%;
  overflow: hidden;
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.a-title {
  font-weight: 500;
  font-size: 50px;
}

.a-desc {
  font-size: 20px;
  width: 40%;
  color: lightgreen;
}
.a-sub {
  margin: 30px 0px;
  width: 40%;
  font-size: 25px;
  background-image: linear-gradient(
    -225deg,
    #231557 3%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 20% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 8s linear infinite;
  display: inline-block;
  font-size: 20px;
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@media screen and (max-width: 480px) {
  .a {
    height: 100vh;
    flex-direction: column;
    text-align: center;
  }
  .a-img {
    padding-top: 50%;
  }
  .a-desc {
    padding-top: 5%;
    padding-left: 30%;
  }
  .a-left {
    width: 100%;
  }

  .a-card {
    height: 50vh;
    padding-right: 30%;
  }

  .a-right {
    padding: 20px;
  }
}
